import React from "react";
import CardHeader from "./CardHeader";
import CardBody from "./CardBody";
import CardButton from "./CardButton";

function Card(props) {
  return (
    <React.Fragment>
      <div className="col pricing-plan-container d-flex justify-content-center mb-5 ">
        <div
          className="col-6 pricing-plan1 d-flex flex-column align-items-center"
          id="pricing_plan_1"
        >
          <CardHeader
            headerInnerTop={props.headerInnerTop}
            headerInnerMiddle={props.headerInnerMiddle}
            headerInnerBottom={props.headerInnerBottom}
            headerMiddle={props.headerMiddle}
            headerBottom={props.headerBottom}
          />
          <CardBody
            bodyItem1={props.bodyItem1}
            bodyItem2={props.bodyItem2}
            bodyItem3={props.bodyItem3}
            bodyItem4={props.bodyItem4}
            bodyItem5={props.bodyItem5}
          />
          <CardButton />
        </div>
      </div>
    </React.Fragment>
  );
}

export default Card;
