import React from "react";
import team from "../images/team-illustration.svg";
import suitcase from "../images/suitcase.svg";
import dollar from "../images/dollar.svg";

function OurExpertise() {
    return (
        <>
            <div className="container animate__animated animate__fadeInRight">
                <div className="row expertise-flex">
                    <div className="col-6">
                        <h5 className="expertise-h5">Our Expertise</h5>
                        <h3 className="expertise-h2">Cloud built by and for <span className="heading-color">Professionals</span></h3>
                        <p className="expertise-p">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
                        <div className="d-flex my-5">
                            <div className="d-flex flex-column">
                                <div className="d-flex">
                                    <div className="suitcase-image"><img src={suitcase} alt="Suitcase logo" className="suitcase-logo" /></div>
                                    <h4 className="expertise-h4">Professionalism</h4>
                            </div>
                                <p className="expertise-p1">We have the best professional marketing people across the globe just to work with you.</p>
                                </div>
                            <div className="d-flex flex-column">
                                <div className="d-flex">
                                    <div className="dollar-image"><img src={dollar} alt="Suitcase logo" className="dollar-logo" /></div>
                                    <h4 className="expertise-h4">Affordable</h4>
                                </div>
                                <p className="expertise-p1">We promise to offer you the best rate we can - at par with the industry standard.</p>
                            </div>
                        </div>
                        <button className="btn btn-primary rounded-pill" id="expertise-button">Learn More</button>
                    </div>
                    <div className="col-6 expertise-item">
                        <img className="team-logo" src={team} alt="Team image" />
                    </div>
                </div>
            </div>
        </>
    );
}

export default OurExpertise;