import React from "react";

function CardBody(props) {
  return (
    <React.Fragment>
      <div className="col pricing-plan-item2 d-flex flex-column mb-3">
        <span className="plan-body mb-4">{props.bodyItem1}</span>
        <span className="plan-body mb-4">{props.bodyItem2}</span>
        <span className="plan-body mb-4">{props.bodyItem3}</span>
        <span className="plan-body mb-4">{props.bodyItem4}</span>
        <span className="plan-body mb-4">{props.bodyItem5}</span>
      </div>
    </React.Fragment>
  );
}

export default CardBody;
