import React from "react";

function CardHeader(props) {
  return (
    <React.Fragment>
      <div className="col pricing-plan-item1 d-flex flex-column">
        <span className="plan-header-top text-center">
          <span className="header-inner-top">{props.headerInnerTop}</span>
          <span className="header-inner-middle">{props.headerInnerMiddle}</span>
          <span className="header-inner-bottom">{props.headerInnerBottom}</span>
        </span>
        <span className="plan-header-middle text-center">
          {props.headerMiddle}
        </span>
        <span className="plan-header-bottom mb-3 text-center">
          {props.headerBottom}
        </span>
      </div>
    </React.Fragment>
  );
}

export default CardHeader;
