import React from "react";
import testimonial from "../images/love-illustration.svg";
import star from "../images/star.svg";
import charlotte from "../images/charlotte.jfif";
import adam from "../images/adam.jfif";
import "./css/testimonial.css";

function Testimonials() {
  return (
    <>
      <div className="container my-5 animate__animated animate__fadeInLeft">
        <div className="row d-flex">
          <div className="col">
            <img
              src={testimonial}
              alt="testimonial svg"
              className="testimonial-logo"
            />
          </div>
          <div className="col d-flex flex-column">
            <div className="col">
              <h5 className="testimonial-h5">Tesimonials</h5>
              <h2 className="testimonial-h2">
                Our Clients <span className="heading-color">Love Us</span>
              </h2>
              <p className="testimonial-p">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua enim
                ad minim veniam.
              </p>
            </div>
            <div className="carousel-container">
              <div className="carousel-item">
                <div className="testimonial-star">
                  <img src={star} alt="star" />
                </div>
                <div className="testimonial-title">Amazing User Experience</div>
                <div className="testimonial-detail">
                  Duis aute irure dolor in reprehenderit in voluptate velit esse
                  cillum dolore eu fugiat nulla pariatur. Excepteur sint
                  occaecat cupidatat non proident, sunt in culpa qui officia.
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud exercitation ullamco.
                </div>
                <div className="d-flex mt-3">
                  <img
                    src={charlotte}
                    alt="charlotte"
                    className="testimonial-charlotte"
                  />
                  <div className="d-flex flex-column ms-3">
                    <div className="testimonial-name">Charlotte Hale</div>
                    <div className="testimonial-post">CEO, Delos Inc.</div>
                  </div>
                </div>
              </div>
              <div className="carousel-item">
                <div className="testimonial-star">
                  <img src={star} alt="star" />
                </div>
                <div className="testimonial-title">
                  Love the Developer Experience and Design Principles!
                </div>
                <div className="testimonial-detail">
                  Sinor Duis aute irure dolor in reprehenderit in voluptate
                  velit esse cillum dolore eu fugiat nulla pariatur. Excepteur
                  sint occaecat cupidatat non proident, sunt in culpa qui
                  officia. Lorem ipsum dolor sit amet, consectetur adipiscing
                  elit, sed do eiusmod tempor incididunt ut labore et dolore
                  magna aliqua. Ut enim ad minim veniam.
                </div>
                <div className="d-flex mt-3">
                  <img src={adam} alt="adam" className="testimonial-adam" />
                  <div className="d-flex flex-column ms-3">
                    <div className="testimonial-name">Adam Cuppy</div>
                    <div className="testimonial-post">Founder, EventsNYC</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Testimonials;

