import React from "react";
import {Link} from "react-router-dom";


function NavBar() {
    return (
        <React.Fragment>
            <nav className="navbar navbar-expand-lg m-4 animate__animated animate__fadeInLeft">
                <div className="container-fluid mb-5" >
                    <Link to="/" class="navbar-brand">
                        <svg width="40" height="40" viewBox="0 0 256 256" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <rect x="10" y="12" width="232" height="232" rx="13" fill="#6415FF"className="me-2"/>
                                    <path d="M78.6614 96.3636H112.786V185H143.144V96.3636H177.269V71.5455H78.6614V96.3636Z" fill="white" />
                                    </svg><span className="ms-3" id="navbar-treact">Treact</span></Link>
                <div className="collapse navbar-collapse d-flex space-between">
                    <ul className="navbar-nav m-auto mb-2 mb-lg-0">
                        <li className="nav-item-middle me-3">
                            <Link to="/about" className="nav-link active" id="navbar-text">About</Link>
                        </li>
                        <li className="nav-item-middle me-3">
                            <Link to="/blog" className="nav-link" id="navbar-text">Blog</Link>
                        </li>
                        <li className="nav-item-middle me-3">
                            <Link to="/pricing" className="nav-link" id="navbar-text">Pricing</Link>
                        </li>
                        <li className="nav-item-middle me-3">
                            <Link to="/contact-us" className="nav-link" id="navbar-text">Contact Us</Link>
                        </li>
                        <li className="nav-item-middle me-3">
                            <Link to="/testimonials" className="nav-link" id="navbar-text">Testimonials</Link>
                        </li>
                    </ul>
                </div>
                    <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                        <li className="nav-item-right">
                            <Link to="/login" className="nav-link" id="navbar-text">Login</Link>
                        </li>
                        <button className="btn btn-lg rounded-pill ms-2" id="button">Sign Up</button>
                    </ul>
                </div>
                
            </nav>
        </React.Fragment>
    );
}

export default NavBar;
