import React from "react";
import Accordion from "./Accordion";

function Faq() {
  const caret = (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      stroke-width="2"
      stroke-linecap="round"
      stroke-linejoin="round"
      class="feather feather-chevron-down"
    >
      <polyline points="6 9 12 15 18 9"></polyline>
    </svg>
  );

  const accordionData = [
    {
      question: "Is lunch provided free of cost ?",
      answer: `Yes, it is, if you have a membership with us. 
                Otherwise it is charged as per the menu. Some limits do apply as to how much items can be included in your lunch. 
                This limit is enough for any one person and merely exists to discourage abusal of the system.`,
    },
    {
      question: "Do you have 2 Bedroom suites ?",
      answer: `Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna 
      aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.`,
    },
    {
      question: "Are Wi-Fi costs included in the price ?",
      answer: `Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna 
      aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.`,
    },
    {
      question: "Where can I reach you for support ?",
      answer: `Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna 
      aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.`,
    },
  ];

  return (
    <React.Fragment>
      <div className="container mb-5 mt-5 animate__animated animate__fadeInLeft">
        <div className="row">
          <div className="col">
            <h5 className="faq-h5 text-center">FAQS</h5>
            <h2 className="faq-h2 text-center">
              Any <span className="heading-color">Questions?</span>
            </h2>
            <h5 className="faq-p text-center">
              And we have got answers to all of them. Lorem ipsum dolor sit
              amet, consectetur adipiscing elit.
            </h5>
            <div className="container">
              <div className="container-accordion">
                {accordionData.map(({ question, answer }) => (
                  <Accordion question={question} answer={answer} />
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Faq;
