import React from "react";

function ContactUs() {
    return (
        <React.Fragment>
            <div className="container-fluid contact-main animate__animated animate__fadeInLeft">
                <div className="container p-5 contact-container">
                    <div className="contact-flex d-flex justify-content-between">
                        <div className="contact-left p-2">
                            <h6 className="contact-h6">Interested in Treact ?</h6>
                            <h5 className="contact-h5">Join the closed beta now.</h5>
                        </div>
                        <div className="contact-right d-flex p-2 align-items-center">
                            <a href="#" className="me-5"><button className="btn btn-lg" id="button-left">Get Started</button></a>
                            <a href="#"><button className="btn btn-lg" id="button-right">Contact Us</button></a>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default ContactUs;