import React, { useState } from "react";

import {
  pricingInfoMonthlyFreePlan,
  pricingInfoMonthlyProPlan,
  pricingInfoYearlyFreePlan,
  pricingInfoYearlyProPlan,
} from "./PricingData";
import Card from "./Card";

function Pricing() {
  const [isActive, setIsActive] = useState(true);

  return (
    <React.Fragment>
      <div
        className="container animate__animated animate__fadeInRight"
        id="pricing-container"
      >
        <div className="row">
          <div className="col-12 text-center pricing-header d-flex flex-column mb-4">
            <h5 className="pricing-h5">Pricing</h5>
            <h2 className="pricing-h2">
              Flexible <span className="heading-color">Plans</span>
            </h2>
            <p className="pricing-p">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua.
            </p>
          </div>
          <div className="container">
            <div className="container">
              <div
                className="btn-switchbox"
                onClick={() => setIsActive(!isActive)}
              >
                {isActive ? (
                  <button className="button-switch1">Monthly</button>
                ) : (
                  <button className="button-switch2">Yearly</button>
                )}
              </div>
            </div>
          </div>
          {isActive ? (
            <div className="d-flex justify-content-center">
              <div>
                {pricingInfoMonthlyFreePlan.map(
                  ({
                    headerInnerTop,
                    headerInnerMiddle,
                    headerInnerBottom,
                    headerMiddle,
                    headerBottom,
                    bodyItem1,
                    bodyItem2,
                    bodyItem3,
                    bodyItem4,
                  }) => (
                    <Card
                      headerInnerTop={headerInnerTop}
                      headerInnerMiddle={headerInnerMiddle}
                      headerInnerBottom={headerInnerBottom}
                      headerMiddle={headerMiddle}
                      headerBottom={headerBottom}
                      bodyItem1={bodyItem1}
                      bodyItem2={bodyItem2}
                      bodyItem3={bodyItem3}
                      bodyItem4={bodyItem4}
                    />
                  )
                )}
              </div>
              <div>
                {pricingInfoMonthlyProPlan.map(
                  ({
                    headerInnerTop,
                    headerInnerMiddle,
                    headerInnerBottom,
                    headerMiddle,
                    headerBottom,
                    bodyItem1,
                    bodyItem2,
                    bodyItem3,
                    bodyItem4,
                    bodyItem5,
                  }) => (
                    <Card
                      headerInnerTop={headerInnerTop}
                      headerInnerMiddle={headerInnerMiddle}
                      headerInnerBottom={headerInnerBottom}
                      headerMiddle={headerMiddle}
                      headerBottom={headerBottom}
                      bodyItem1={bodyItem1}
                      bodyItem2={bodyItem2}
                      bodyItem3={bodyItem3}
                      bodyItem4={bodyItem4}
                      bodyItem5={bodyItem5}
                    />
                  )
                )}
              </div>
            </div>
          ) : (
            <div className="d-flex justify-content-center">
              <div>
                {pricingInfoYearlyFreePlan.map(
                  ({
                    headerInnerTop,
                    headerInnerMiddle,
                    headerInnerBottom,
                    headerMiddle,
                    headerBottom,
                    bodyItem1,
                    bodyItem2,
                    bodyItem3,
                    bodyItem4,
                  }) => (
                    <Card
                      headerInnerTop={headerInnerTop}
                      headerInnerMiddle={headerInnerMiddle}
                      headerInnerBottom={headerInnerBottom}
                      headerMiddle={headerMiddle}
                      headerBottom={headerBottom}
                      bodyItem1={bodyItem1}
                      bodyItem2={bodyItem2}
                      bodyItem3={bodyItem3}
                      bodyItem4={bodyItem4}
                    />
                  )
                )}
              </div>
              <div>
                {pricingInfoYearlyProPlan.map(
                  ({
                    headerInnerTop,
                    headerInnerMiddle,
                    headerInnerBottom,
                    headerMiddle,
                    headerBottom,
                    bodyItem1,
                    bodyItem2,
                    bodyItem3,
                    bodyItem4,
                    bodyItem5,
                  }) => (
                    <Card
                      headerInnerTop={headerInnerTop}
                      headerInnerMiddle={headerInnerMiddle}
                      headerInnerBottom={headerInnerBottom}
                      headerMiddle={headerMiddle}
                      headerBottom={headerBottom}
                      bodyItem1={bodyItem1}
                      bodyItem2={bodyItem2}
                      bodyItem3={bodyItem3}
                      bodyItem4={bodyItem4}
                      bodyItem5={bodyItem5}
                    />
                  )
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    </React.Fragment>
  );
}

export default Pricing;
